import React, { useEffect , useState} from "react";
//import repos from "../data/repos";
import Repo from "./repo";

export default () => {
    
    const [repos, setRepos] = useState([]);
    const [reposCount, setReposCount] = useState([]);

    useEffect(() => {

        const data = sessionStorage.getItem("repos");
        let myRepos;
        if(data){
            myRepos = JSON.parse(data);
            setReposCount(myRepos.length);
            myRepos = myRepos.slice(1,10);

            return setRepos(myRepos);
        }
        

        async function fetchRepos() {
            const response = await fetch ("https://api.github.com/users/guerrasoto10/repos");
            let myRepos = await response.json();
            setReposCount(myRepos.length);
            myRepos = myRepos.slice(1,10);
            
            sessionStorage.setItem("repos", JSON.stringify(myRepos));

            setRepos(myRepos);
        }
        fetchRepos();
    },[]);
    return(
        <div className="container mx-auto p-12 max-w-4xl">
            <header className="text-center">
                <h2 className="text-3xl font-bold">Mis repositorios en Github</h2>
                <p>Estos son los repos mas recientes.</p>
            </header>
            <ul className="repos-list mt-6">
                {
                    repos.map((repo)=>{
                    return <Repo repo={repo} key={repo.id} />
                    })
                }
            </ul>
            <div className="mt-8 text-center">
                <a href="https://github.com/guerrasoto10/" className="btn" target="_blank" rel="noopener noreferrer">
                    Verlos todos en Github ({reposCount})
                </a>
            </div>
        </div>
    );
};